<template>
  <div class="my-swiper-cover" ref="smallSwipr">
    <div
      :class="['my-swiper-content', { noTransition: noTransition }]"
      :style="{ left: slideLeft + 'px' }"
      @transitionend="transitionend"
    >
      <!-- 假 -->
      <div class="my-swiper-content-item">
        <slot name="4"></slot>
      </div>
      <div class="my-swiper-content-item">
        <slot name="1"></slot>
      </div>
      <div class="my-swiper-content-item">
        <slot name="2"></slot>
      </div>
      <div class="my-swiper-content-item">
        <slot name="3"></slot>
      </div>
      <div class="my-swiper-content-item">
        <slot name="4"></slot>
      </div>
      <!-- 假 -->
      <div class="my-swiper-content-item">
        <slot name="1"></slot>
      </div>
    </div>
    <div class="my-swiper-btn">
      <div
        :class="{ selected: currentIdx === index }"
        v-for="(item, index) in arr"
        :key="item"
        @click="handleSlide(index)"
      ></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "MySwiper",
  props: {
    ItemWidth: {
      type: Number,
    },
  },
  data() {
    return {
      show: false,
      toLeft: {
        display: "animate__fadeInRight",
        hidden: "animate__fadeOutLeft",
      },
      toRight: [],
      toTop: [],
      toBottom: [],
      currentIdx: 0,
      arr: [1, 2, 3, 4],
      noTransition: false,
      slideLeft: 0,
    };
  },
  watch: {
    currentIdx: function (newval, oldval) {
      // console.log(this.arr.length)
      const finalIdx = this.arr.length - 1;
      //  从最后一个点到第一个
      if (newval === 0 && oldval === finalIdx) {
        console.warn("从最后一个点到第一个");
        this.slideLeft = this.slideLeft - this.ItemWidth;
      }
      //从第一个点到最后一个
      else if (newval === finalIdx && oldval === 0) {
        this.slideLeft = this.slideLeft + this.ItemWidth;
        console.warn("从第一个点到最后一个", this.slideLeft);
      } else {
        this.slideLeft = -((newval + 1) * this.ItemWidth);
      }
    },
    ItemWidth: (newval) => {
      if (newval) {
        document.querySelectorAll(".my-swiper-content-item").forEach((item) => {
          item.style.width = newval + "px";
        });
      }
    },
  },
  mounted() {
              this.slideLeft = -this.ItemWidth;

  },
  methods: {
    handleSlide(idx) {
      this.currentIdx = idx;
    },
    transitionend() {
      console.warn("transitionend", this.slideLeft);
      // 点到最后一张假图
      if (this.slideLeft === -2900) {
        this.noTransition = true;
        this.$nextTick(() => {
          this.slideLeft = -this.ItemWidth;
          setTimeout(() => {
            console.warn("加上动画");
            this.noTransition = false;
          }, 1000);
        });
      }
      // 点到第一张假图
      if (this.slideLeft === 0) {
        this.noTransition = true;
        this.$nextTick(() => {
          this.slideLeft = -(this.arr.length - 1) * this.ItemWidth;
          setTimeout(() => {
            console.warn("加上动画");
            this.noTransition = false;
          }, 1000);
        });
      }
    },
  },
};
</script>
<style lang="less" scoped>
.my-swiper-cover {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;

  .my-swiper-content {
    overflow: hidden;
    position: relative;
    position: absolute;
    display: inline-block;
    white-space: nowrap;
    left: 0;
    top: 0;
    bottom: 60px;
    transition: left 1s;
    .my-swiper-content-item {
      display: inline-block;
      white-space: normal;
      padding-right: 3px;
      width: 100%;
      height: 100%;
      color: rgba(255, 255, 255, 0.6);
      .bottom {
        .sub {
          color: #fff;
        }
        span {
          display: block;
          line-height: 22px;
        }
      }
    }
    &.noTransition {
      transition: none;
    }
  }
  .my-swiper-btn {
    display: flex;
    position: absolute;
    bottom: 0;
    left: 0;
    div {
      width: 30px;
      height: 30px;
      position: relative;
      &::after {
        content: "";
        display: block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        position: absolute;
        background-color: #fff;
        box-shadow: inset 0 0 0 5px #fff;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
      &.selected {
        &::after {
          transform: translate(-50%, -50%) scale(2);
          transition: all 300ms;
          box-shadow: inset 0 0 0 1px #ff1053;
          background-color: transparent;
        }
      }
    }
  }
}
</style>