<template>
  <div id="app">
    <TopHeader v-show="!hasMenuChecked" @handleCheckMenu="handleCheckMenu" />
    <ItemMenu v-show="hasMenuChecked" />
    <FullPageOptions
      :swiperBtns="swiperBtns"
      v-show="!hasMenuChecked"
      @slideTo="slideTo"
      :currentBtnIdx="currentBtnIdx"
    />
    <mv-full-page
      :isPointer="false"
      :isV="isV"
      :pages="swiperBtns.length"
      :page.sync="currentBtnIdx"
      :isCache="false"
      :transition="{
        duration: '700ms', // 动画时长
        timingFun: 'ease', // 动画速度曲线
        delay: '0s', // 动画延迟
      }"
      @rollEnd="rollEnd"
      ref="mySwiper"
    >
      <template #page1>
        <FullPageItem
          @closeSlideHandle="handleCheckMenu"
          :hasMenuChecked="hasMenuChecked"
        >
          <PageViedo :class="hasMenuChecked ? 'blur' : ''" />
        </FullPageItem>
      </template>
      <template #page2>
        <FullPageItem
          @closeSlideHandle="handleCheckMenu"
          :hasMenuChecked="hasMenuChecked"
        >
          <PageTwo
            :startAnimation="currentBtnIdx === 2"
            :class="hasMenuChecked ? 'blur' : ''"
          />
        </FullPageItem>
      </template>
      <template #page3>
        <FullPageItem
          @closeSlideHandle="handleCheckMenu"
          :hasMenuChecked="hasMenuChecked"
        >
          <PageThird
            :startAnimation="currentBtnIdx === 3"
            :class="hasMenuChecked ? 'blur' : ''"
          />
        </FullPageItem>
      </template>
      <template #page4>
        <FullPageItem
          @closeSlideHandle="handleCheckMenu"
          :hasMenuChecked="hasMenuChecked"
        >
          <PageForth   
            :startAnimation="currentBtnIdx === 4"
            :class="hasMenuChecked ? 'blur' : ''"/>
        </FullPageItem>
      </template>
      <template #page5>
        <FullPageItem
          @closeSlideHandle="handleCheckMenu"
          :hasMenuChecked="hasMenuChecked"
        >
          <PageFifth
            :startAnimation="currentBtnIdx === 5"
            :class="hasMenuChecked ? 'blur' : ''"
          />
        </FullPageItem>
      </template>

      <template #page6>
        <FullPageItem
          @closeSlideHandle="handleCheckMenu"
          :hasMenuChecked="hasMenuChecked"
        >
          <PageSixth 
           :startAnimation="currentBtnIdx === 6"
            :class="hasMenuChecked ? 'blur' : ''"
          />
        </FullPageItem>
      </template>
    </mv-full-page>
  </div>
</template>

<script>
import "./assets/css/main.less";
import "./assets/iconfont/iconfont.css";
import PageViedo from "./components/PageViedo";
import TopHeader from "./components/TopHeader";
import PageThird from "./components/PageThird";
import PageFifth from "./components/PageFifth";
import ItemMenu from "./components/ItemMenu";
import FullPageItem from "./components/FullPageItem";
import FullPageOptions from "./components/FullPageOptions";
import PageTwo from "./components/PageTwo";
import PageForth from "./components/PageForth"
import PageSixth from "./components/PageSixth"
export default {
  name: "App",
  components: {
    TopHeader,
    PageViedo,
    PageThird,
    PageFifth,
    ItemMenu,
    FullPageItem,
    FullPageOptions,
    PageTwo,
    PageForth,
    PageSixth
  },
  data() {
    return {
      hasMenuChecked: false,
      isV: true,
      currentBtnIdx: 1,
      swiperBtns: ["Main", "Introduction", "Advantage", "Tokenomics", "Roadmap","Buy$OWL"],
    };
  },
  methods: {
    // 监听滑动
    rollEnd(pageidx) {
      this.currentBtnIdx = pageidx;
      console.warn(pageidx);
    },
    // 滑动指定页面
    slideTo(idx) {
      console.warn("index", idx);
      this.$refs.mySwiper.pointerClick(idx);
    },
    // swiper缩小
    handleCheckMenu() {
      console.warn("handleCheckMenu");
      this.hasMenuChecked = !this.hasMenuChecked;
    },
  },
  mounted() {
    // mock
    // this.$refs.mySwiper.removePageListener();
    console.warn(this.$refs.mySwiper);
  },

  watch: {
    hasMenuChecked(newval) {
      console.warn("newval");
      if (newval) {
        this.$refs.mySwiper.removePageListener();
        const dom = document.querySelectorAll(".full-page-wrapper .page-box")[
          this.currentBtnIdx - 1
        ];
        // dom.style.width = "100%";
        // dom.style.height = "70%";
        // dom.style.top = "50%";
        // dom.style.left = "-30%";
        // dom.style.transform = "translateY(-50%)";
         dom.style.width = "120%";
        // dom.style.top = "50%";
        dom.style.right = "20%";
        dom.style.transform = "scale(0.8)";
      } else {
        this.$refs.mySwiper.initPageListener();
        const dom = document.querySelectorAll(".full-page-wrapper .page-box")[
          this.currentBtnIdx - 1
        ];
        // dom.style.width = "100%";
        // dom.style.height = "100%";
        // dom.style.top = 0;
        // dom.style.left = 0;
        // dom.style.transform = "none";
            dom.style.width = "100%";
        dom.style.right = "0";
        dom.style.transform = "none";
      }
    },
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
  font-size: 18px;
  line-height: 26px;
}
.full-page-wrapper {
  background-image: linear-gradient(145deg, #00071c, #0f0f0f) !important;
}
.full-page-wrapper .page {
  background-position: center;
}
#app {
  height: 100%;
  color: #fff;
  font-family: PT Serif;
}

</style>
